import React from 'react';
import { NavLink } from 'react-router-dom';

const Navbar = () => {
    return (
        <nav className="navbar navbar-expand-lg px-5 py-1 navbar-dark bg-dark z-3">
            {/* <a className="navbar-brand font-weight-bold" href="#" style={{userSelect:'none',fontSize: '1.5rem', letterSpacing: '.1rem'}}> */}
                {/* Michał Racino */}
            {/* </a> */}
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
                <ul className="navbar-nav" style={{marginLeft: 'auto'}}>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/" exact activeClassName="active">Strona Główna</NavLink>
                    </li>
                    {/* <li className='nav-item'> */}
                        {/* <NavLink className="nav-link" to="/about-me" exact activeClassName="active">O mnie</NavLink> */}
                    {/* </li> */}
                    <li className='nav-item'>
                        <NavLink className="nav-link" to="/contact" exact activeClassName="active">Kontakt</NavLink>
                    </li>
                </ul>
            </div>
        </nav>
    );
}

export default Navbar;

