import React, { useState, useEffect } from 'react';

const Clients = () => {
    const clients = [
        {
            'logo': 'https://perfectklima.pl/wp-content/uploads/2023/10/perfect-klima-wide-1.png',
            'name': 'Perfect Klima',
            'description': 'Strona internetowa przedstawiająca ofertę firmy zajmującej się klimatyzacją i wentylacją.',
            'url': 'https://perfectklima.pl'
        },
        // {
        //     'logo':'',
        //     'name':'Apartments in Poland',
        //     'description':'Firma zajmująca się wynajmem apartamentów',
        //     'url':'apartments.szczepaniuk.pl'
        // },
        {
            'logo':'//myseatbeltguy.com/cdn/shop/files/Frame_41_1.svg?v=1712005533&width=600',
            'name':'My Seat Belt Guy',
            'description':'API dla kanadyjskiego sklepu internetowego, obsługujące wysyłki produktów do i od klientów.',
            'url':'https://myseatbeltguy.com'
        },
        {
            'logo': 'https://upload.wikimedia.org/wikipedia/commons/3/34/Microsoft_Office_Excel_%282019%E2%80%93present%29.svg',
            'name': 'Excel',
            'description': 'Realizuję również projekty w Excelu, takie jak kalkulatory, analizy danych, czy automatyzacje.',
        }
    ];

    const [currentClientIndex, setCurrentClientIndex] = useState(0);
    const [progress, setProgress] = useState(0);

    useEffect(() => {
        const progressInterval = setInterval(() => {
            setProgress((progress) => (progress + 1)%101);
        }, 100)
    }, [clients.length]);
    useEffect(()=>{
        if(progress === 100){
            setProgress(0);
            setCurrentClientIndex((currentClientIndex) => (currentClientIndex + 1) % clients.length);
        }
    }, [progress]);
    const currentClient = clients[currentClientIndex];

    return (
        <div className="bg-dark text-light w-100" style={{border: 'none'}}>
            <a href={currentClient.url} target='blank'>
                <div className="card-img-top p-3" style={{ width: '100%',height: '10rem', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <img src={currentClient.logo} alt={currentClient.name} style={{ height: '100%', width: '100%' }} />
                </div>
            </a>
            <div className="card-body p-3">
                <p className="card-text">
                    {currentClient.description}<br />
                </p>
            </div>
            {currentClient.url != undefined ? <small className='text-center'>Aby zobaczyć stronę klienta, kliknij <a className='text-light' href={currentClient.url} target='blank'>tutaj</a> lub na logo</small>:<small>&nbsp;<br /> &nbsp;</small>}
            <div className="progress mt-1" style={{height: '5px'}}>
            <div
                className="progress-bar bg-secondary"
                role="progressbar"
                style={{ width: `${progress}%` }}
                aria-valuenow={progress}
            ></div></div>
        </div>
    );
};

export default Clients;