import { useEffect, useState } from "react";
import { Prism as SyntaxHighlighter} from 'react-syntax-highlighter';
import { twilight } from "react-syntax-highlighter/dist/esm/styles/prism";
const CodeBackground = () => {
    const [code, setCode] = useState('');
    useEffect(()=>{
        fetch('/code.txt')
        .then((response) => response.text())
        .then((data) => setCode(data));
    }, [])
    const [textToDisplay, setTextToDisplay] = useState("");
    useEffect(() => {
        const interval = setInterval(() => {
            setTextToDisplay((prevText) => {
                if (prevText.length === code.length) {
                    setTextToDisplay('');
                }
                return prevText + code[prevText.length];
            });
        }, 50);
        return () => clearInterval(interval);
    }, [code]);
    return (
        <div
        style={{
            position: "absolute",
            width: "100%",
            height: "100%",
            top: 0,
            left: 0,
            // background: "rgba(0,0,0,0.7)",
            zIndex: 1,
            overflow: "hidden",
            padding: "1rem",
            opacity: 0.5,
            overflow: "hidden",
            textAlign: "justify",
            
            pointerEvents: "none", // Nie pozwala na interakcję
        }}
    >
        <SyntaxHighlighter 
            wrapLines 
            wrapLongLines 
            language="javascript" 
            style={twilight} 
            customStyle={{
                boxShadow: 'none',
                overflow: 'hidden',
                background: '',
                border: 'none',
                outline: 'none',
                fontSize: window.innerWidth > 768 ? '0.7rem' : '0.5rem', 
                height: '100%', 
                width: '100%' 
            }}
        >
            {textToDisplay}
        </SyntaxHighlighter>
    </div>
    );
};

export default CodeBackground;