import { motion } from "framer-motion";
import ContactForm from "../components/ContactForm";
const Contact = () => {
    return (
        <div className="text-light container-fluid p-5" style={{backgroundImage:'url("tlo_ux.png")', backgroundSize: 'cover', backgroundPosition: 'center', backgroundAttachment: 'fixed'}}>
        <motion.div
            className="row fullScreen" 
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1 }}
            style={{ zIndex: 2 }}
        >
            <div className="col-md-6 p-3">
                <h1 className="text-center">Kontakt</h1>
                <p>
                    Zapraszam do kontaktu w celu wyceny. Dla ułatwienia wyceny, proszę przygotować:
                    <ul>
                        <li>Opis pożądanego efektu końcowego</li>
                        <li>Wizualizację wyglądu strony, zamysł stylistyczny (opcjonalnie)</li>
                        <li>Informację o preferowanych technologiach do zastosowania (opcjonalnie)</li>
                    </ul>
                </p>
            </div>
            <div className="col-md-6">  
                <ContactForm />
            </div>
        </motion.div>
    </div>
    );
}
export default Contact;