import { useState, useEffect } from "react";
import { motion } from "framer-motion";

const ContactForm = () => {
    const [selector, setSelector] = useState(null);
    const [selectedContactForm, setSelectedContactForm] = useState(null);
    const [content, setContent] = useState(null);
    const contactForms = [
        {
            'icon': '/kontakt_telefoniczny.png',
            'name': 'Telefon',
            'form': <div className="p-2">
                <button type="button" class="btn-close btn-close-white float-end" aria-label="Close" onClick={()=>{setSelectedContactForm(null)}}></button>
                <h2>Kontakt telefoniczny</h2>
                <p>
                    <a href="tel:+48667069648" className="text-light"><i class="bi bi-telephone"></i> Numer: +48 667 069 648<br /></a>
                    <i class="bi bi-clock"></i> Dostępność: Pon-Pt, 9:00-17:00
                </p>
            </div>
        },
        {
            'icon': '/kontakt_mailowy.png',
            'name': 'E-mail',
            'form': <div className="p-2">
            <button type="button" class="btn-close btn-close-white float-end" aria-label="Close" onClick={()=>{setSelectedContactForm(null)}}></button>
            <h2>Kontakt E-Mail</h2>
            <p>
                <a href="mailto:michal.racino3@gmail.com" className="text-light"><i class="bi bi-envelope"></i> Adres: michal.racino3@gmail.com<br /></a>
                W treści maila proszę o krótkie opisanie zamysłu strony internetowej
            </p>
        </div>
        },
    ]
    useEffect(()=>{
        setSelector([<small className="text-center">Wybierz formę kontaktu</small>]);
        contactForms.forEach((element, index) => {
            setSelector((oldState)=>[...oldState, <div onClick={()=>{selectedContactForm==index?setSelectedContactForm(null):setSelectedContactForm(index)}} style={{cursor: 'pointer'}} className={"col-md-3 mx-1 mt-1 border rounded p-2 user-select-none "+(index==selectedContactForm?" bg-primary":"")}><h2 className="text-center">{element.name}</h2><img src={element.icon} style={{maxWidth: '100%',maxHeight: '100%'}}></img></div>])
        });
    }, [selectedContactForm]);
    useEffect(()=>{
        if(selectedContactForm == null){
            setContent(selector);
            return;
        }
        setContent(<motion.div initial={{ opacity: 0, y: 30 }} animate={{ opacity: 1, y: 0 }} transition={{duration: .5}} >{contactForms[selectedContactForm].form}</motion.div>)
    }, [selectedContactForm, selector])

    return(<div className="my-5 py-5 row justify-content-center">
        {content}
    </div>)

}
export default ContactForm